import { makeAction } from "../util";

const SET_GITHUB_USER = "SET_GITHUB_USER";
const SET_ACCESS_TOKEN = "SET_ACCESS_TOKEN";
const SET_TOKEN_USER = "SET_TOKEN_USER";
const LS_KEY_ACCESS_TOKEN = "LS_KEY_ACCESS_TOKEN";

const state = {
  accessToken: localStorage.getItem(LS_KEY_ACCESS_TOKEN), // 当前登录用户的 GitHub AccessToken
  auth: {
    proxy:
      "https://cors-anywhere.herokuapp.com/https://github.com/login/oauth/access_token",
    clientID: "b52f163d871a24e1910e", //  OAuth application 的 Client ID
    clientSecret: "92cafd1848f1a5ec72b3a7deef4942a240ce8f1e", // OAuth application 的 Client Secret
  },
  gitHubUser: null,
  gitHubUsername: "kelyu0", // GitHub 账号
  repo: "kelyu0/articles", // 记录 Issue 的仓库的全名「用户名/仓库名」
  pageSize: 10, // 博客列表每页显示多少条博客
  thirdPartySite: [
    // 左上角展示的第三方站点信息
    {
      img: require('../../assets/github.png'), // 第三方站点图标，存放在「vue_blog/static/img」目录中
      url: "https://github.com/kelyu0", // 第三方站点的 url
    },
    {
      img: require('../../assets/linkedin.png'),
      url: "https://www.linkedin.com/in/ke-lyu-638bb8141/",
    },
  ],
};

const mutations = {
  [SET_GITHUB_USER](state, gitHubUser) {
    state.gitHubUser = gitHubUser;
  },
  [SET_ACCESS_TOKEN](state, accessToken) {
    state.accessToken = accessToken;
    localStorage.setItem(LS_KEY_ACCESS_TOKEN, accessToken);
  },
  [SET_TOKEN_USER](state, tokenUser) {
    state.tokenUser = tokenUser;
  },
};

const actions = {
  setGitHubUser: makeAction(SET_GITHUB_USER),
  setAccessToken: makeAction(SET_ACCESS_TOKEN),
};

const getters = {
  gitHubUsername(state) {
    return state.gitHubUsername;
  },
  repo(state) {
    return state.repo;
  },
  gitHubUser(state) {
    return state.gitHubUser;
  },
  thirdPartySite(state) {
    return state.thirdPartySite;
  },
  pageSize(state) {
    return state.pageSize;
  },
  auth(state) {
    return state.auth;
  },
  accessToken(state) {
    return state.accessToken;
  },
  loginLink(state) {
    const query = {
      client_id: state.auth.clientID,
      redirect_uri: location.href,
      scope: "public_repo",
    };

    const queryString = Object.keys(query)
      .map((key) => `${key}=${encodeURIComponent(query[key] || "")}`)
      .join("&");

    return `http://github.com/login/oauth/authorize?${queryString}`;
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
